.floating-title-container {

    position: absolute;
    margin-top: 14%;
    margin-left: 10%;
    
    .floating-title {
     

        transform: rotate(270deg);
        transform-origin: 0 0;

        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 1.6px;
        text-transform: uppercase;
    }

    @include media-breakpoint-down(lg) {
        display: none;
    }
}